.onsale {
	@apply card__label bg-green-500 text-white;
	@apply inline-block px-3 py-2 text-sm font-bold text-white;

	.onsale__label {
	}

	.onsale__percentage {
		@apply hidden sm:inline-block;
	}

	.woocommerce-product-media & {
		@apply absolute top-3 left-3;
	}
}

.woocommerce-product-media {
	@apply relative;
}
